

















import { Component, Vue } from "vue-property-decorator";
@Component({
  components: {}
})
export default class About extends Vue {
  name: any = "init name";
  mounted() {
    // 增加state监听，当mt数据发生变化的时候，我们修改name，体现在页面上
    const that: any = this;
    that.$onGlobalStateChange((state: any, prev: any) => {
      console.log(state, prev);
      if (state.mt !== prev.mt) {
        that.name = state.mt;
      }
    });
  }
  testFn() {
    const that: any = this;
    that.$setGlobalState({
      mt: "xxx"
    });
    that.$_LOGIN("XXXX");
    console.log("测试事件成功");
  }
}
